import React, {Component} from 'react';
import {withStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';

const useStyles = (theme) => ({
      root: {
            float: 'right'
      }
});

class DocuLink extends Component {
      render() {
            const {classes} = this.props;
            if (typeof this.props.link != 'undefined') {
                  return (
                        <Typography className={classes.root} align="right" variant="subtitle1" gutterBottom>
                              <Link rel="noopener" target="_blank" href={this.props.link}>
                                    Documentation Link
                              </Link>
                        </Typography>
                  );
            } else {
                  return <div></div>;
            }
      }
}

export default withStyles(useStyles)(DocuLink);
